.banner {
    width: 100%;
    padding: 50px 0;
    @media screen and (max-width: $md) {
        padding: 30px 0;
    }
}

.banner__content {
    display: flex;
    @media screen and (min-width: $sm) {
        align-items: center;
    }
    @media screen and (max-width: $sm) {
        flex-direction: column-reverse;
    }
}

.banner__col {
    @media screen and (min-width: $sm) {
        flex: 1 1 50%;
        max-width: 50%;
        &:not(.banner__col--media) {
            padding-right: 80px;
        }
    }
    @media screen and (max-width: $sm) {
        text-align: center;
    }
}

.banner__col--media {
    @media screen and (min-width: $sm) {
        .banner__title {
            display: none;
        }
    }
    @media screen and (max-width: $sm) {
        position: relative;
        .banner__title {
            position: absolute;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            margin-bottom: 0;
        }
    }
}

.banner__title {
    text-transform: uppercase;
}

.banner__title-text--gradient {
    background: $gradient;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    @media screen and (max-width: $sm) {
        display: none;
    }
}

.banner__title-text--white {
    color: $white;
}

.banner__text {
    color: $light-grey;
    margin-bottom: 40px;
}

.banner__img {
    width: 100%;
}